<template>
  <div style="background-color: #f5f5f5;">
    <van-nav-bar :title="tag" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
      <div slot="right" class="top_icon_box" style="height: 46px;">
        <img src="../../assets/cart_icon.png" @click="goCart" />
      </div>
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <div class="banner_box">
      <img v-if="dtlImg && !canToggleDtlImg && !dtlImgLoading" v-lazy="dtlImg" />
      <img v-if="dtlImg && canToggleDtlImg && !dtlImgLoading" :src="dtlImg" :onerror="imgOnerror" />
    </div>
    
    <!--限时-->
    <div class="timelimit_tips" v-if="template == 202">
      <div class="timelimit_tips_0" v-for="(item,index) in timelimitTips" :key="index">
        <div :class="item._isActive ? 'timelimit_tips_0_txt_active timelimit_tips_0_txt':'timelimit_tips_0_txt'">
          <div>{{item._time}}</div>
          <div>{{item._txt}}</div>
        </div>
        <div :class="item._isActive ? 'timelimit_tips_angle_show':''"></div>
      </div>
    </div>
    <!--团购-->
    <div class="group_tips" v-else-if="template == 203">
      <div class="group_tips_times">
        <div class="group_tips_times_0">距离结束还剩</div>
        <div class="group_tips_times_1">
          {{countDown.h}}
          <span>时</span>
          {{countDown.m}}
          <span>分</span>
          {{countDown.s}}
          <span>秒</span>
        </div>
      </div>
      <div class="group_tips_grade">
        <div class="group_tips_grade_0">
          <div>折扣</div>
          <div>人数</div>
        </div>
        <div class="group_tips_grade_1">
          <div class="group_tips_grade_1_0">
            <div v-for="(item,index) in teamBuyWidth" :key="index" :style="'width:'+item._width">{{item.offer}}</div>
          </div>
          <div class="group_tips_grade_1_1">
            <div :style="'width:'+fillWidth"></div>
          </div>
          <div class="group_tips_grade_1_0">
            <div v-for="(item,index) in teamBuyWidth" :key="index" :style="'width:'+item._width">{{item.ladnum}}</div>
          </div>
        </div>
        <div class="group_tips_grade_0">
          <div>折</div>
          <div>人</div>
        </div>
      </div>
    </div>
    <!--品牌-->
    <div :class="{'brand-tips_hide':hideBrandBox,'brand-tips':true}" v-else-if="template == 204">
      <div class="control_btn" @click="showBrandBox">全部品牌<i :class="hideBrandBox?'down':'top'"></i></div>
      <div class="only-activity-box" @click="toggleOnlyActivity">
        <!-- <van-checkbox v-model="onlyActivity" @click="toggleOnlyActivity" checked-color="red">活动</van-checkbox> -->
        <van-icon :name="onlyActivity?'passed':'circle'" size="16px" :color="onlyActivity?'red':'#999999'" />
        <div :style="onlyActivity?'color:red':'color:#999999'">活动</div>
      </div>
      <div v-for="(item,idx) in brandList" :key="idx">
        <div :style="item._selected?'background-color:#ff0000;color:#ffffff':''" @click="fn_set_brand(idx)">{{item.brandname}}</div>
      </div>
    </div>
    
    <!--商品列表-->
    <!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh"> -->
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="goodsDataList.length == 0 ? '' :'没有更多了'"
        @load="onLoad"
      >
        <div v-if="template == 204">
          <div v-for="(item,index) in goodsDataList" :key="index">
            <div class="brand-name">
              <div class="activity_title">{{item.name}}</div>
              <a href="javascript:;" class="activity_all_goods" @click="toBrandZone(item.name,item.manuNo)">
                更多
                <van-icon class="activity_all_icon" name="arrow" size="8" />
              </a>
            </div>
            <div class="activity_box">
              <div v-for="(item,index) in item.data" :key="index" class="goods_box">
                <div class="goods_img_box" @click="toDetail(item)">
                  <img v-lazy='item.imgURl' />
                  <div v-if="item.store == 0">已缺货</div>
                  <!-- <span class="consell-tag" v-if="item.consell & 1 == 1">控销药</span> -->
                  <img class="consell-tag" v-if="(item.consell & 2) > 0" src="..\..\assets\consell-tag-1.png" />
                  <img class="consell-tag" v-else-if="(item.consell & 1) > 0" src="..\..\assets\consell-tag-0.png" />
                </div>
                <div class="goods_info">
                  <div class="goods_title">{{item.prodname}}<span>{{item.spec}}</span></div>
                  <div class="goods_supplier">{{item.manuName}}</div>
                  <div v-if="(item.ladOffDesc && item.ladOffDesc.length>0)||((item.rulestatus & 2048) > 0)||((item.rulestatus & 4096) > 0)" class="goods-activity-description padding-l-r-20">
                    <div v-if="item.ladOffDesc && item.ladOffDesc.length>0" style="margin-right: 4px;">{{item.ladOffDesc[0]}}</div>
                    <div v-if="item.isneareffect" style="margin-right: 4px;">近效期</div>
                    <div v-if="(item.rulestatus & 2048) > 0" style="margin-right: 4px;">秒杀</div>
                    <div v-if="(item.rulestatus & 4096) > 0">团购</div>
                  </div>
                  <div>
                    <div class="goods_tag_box">
                      <!-- <div class="goods_tag">{{item._tag}}</div> -->
                      <div class="goods_spe">中包装：{{item.medpacknum}}{{item.unitName}}</div>
                    </div>
                    <div class="goods_price_box">
                      <div class="goods_price" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                        <div class="actual_price">￥{{item.purchaseprice}}</div>
                        <div class="original_price_0" v-if="item.cart>0||item._showInput">...</div>
                        <div class="original_price" v-else>￥{{item.mp}}</div>
                      </div>
                      <div class="goods_price" v-else>
                        <div class="actual_price fontsize20" v-if="item.purchaseprice == -1">￥认证后可见</div>
                        <div class="actual_price fontsize20" v-if="item.purchaseprice == -2">￥签约后可见</div>
                      </div>
                      <div class="goods_num">
                        <div class="goods_num_minus" :style="item.cart>0||item._showInput?'display:block':'display:none'" @click="fn_minus(item)">-</div>
                        <input type="number" v-model="item.cart" @blur="fn_blur(item)" @focus="fn_focus(item)" :style="item.cart>0||item._showInput?'display:block':'display:none'" />
                        <div class="goods_num_add" @click="fn_add(item)">+</div>
                      </div>
                    </div>
                    <div class="goods_count_tag" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                      <div>毛利率  {{item.grossProfit}}%</div>
                      <div>零售价  {{item.rrp}}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        
        <div class="activity_box" v-else>
          <div v-for="(item,index) in goodsDataList" :key="index" class="goods_box">
            <div class="goods_img_box" @click="toDetail(item)">
              <img v-lazy='item.imgURl' />
              <div v-if="item.store == 0">已缺货</div>
              <!-- <span class="consell-tag" v-if="item.consell & 1 == 1">控销药</span> -->
              <img class="consell-tag" v-if="(item.consell & 2) > 0" src="..\..\assets\consell-tag-1.png" />
              <img class="consell-tag" v-else-if="(item.consell & 1) > 0" src="..\..\assets\consell-tag-0.png" />
            </div>
            <div class="goods_info">
              <div class="goods_title">{{item.prodname}}<span>{{item.spec}}</span></div>
              <div class="goods_supplier">{{item.manuName}}</div>
              <div v-if="(item.ladOffDesc && item.ladOffDesc.length>0)||((item.rulestatus & 2048) > 0)||((item.rulestatus & 4096) > 0)" class="goods-activity-description padding-l-r-20">
                <div v-if="item.ladOffDesc && item.ladOffDesc.length>0" style="margin-right: 4px;">{{item.ladOffDesc[0]}}</div>
                <div v-if="item.isneareffect" style="margin-right: 4px;">近效期</div>
                <div v-if="(item.rulestatus & 2048) > 0" style="margin-right: 4px;">秒杀</div>
                <div v-if="(item.rulestatus & 4096) > 0">团购</div>
              </div>
              <!--新品,热销，包邮，新人专享，中华名方，为你精选,品牌专区-->
              <div v-if="template==201 || template == 204">
                <div class="goods_tag_box">
                  <!-- <div class="goods_tag">{{item._tag}}</div> -->
                  <div class="goods_spe">中包装：{{item.medpacknum}}{{item.unitName}}</div>
                </div>
                <div class="goods_price_box">
                  <div class="goods_price" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                    <div class="actual_price">￥{{item.purchaseprice}}</div>
                    <div class="original_price_0" v-if="item.cart>0||item._showInput">...</div>
                    <div class="original_price" v-else>￥{{item.mp}}</div>
                  </div>
                  <div class="goods_price" v-else>
                    <div class="actual_price fontsize20" v-if="item.purchaseprice == -1">￥认证后可见</div>
                    <div class="actual_price fontsize20" v-if="item.purchaseprice == -2">￥签约后可见</div>
                  </div>
                  <div class="goods_num">
                    <div class="goods_num_minus" :style="item.cart>0||item._showInput?'display:block':'display:none'" @click="fn_minus(item)">-</div>
                    <input type="number" v-model="item.cart" @blur="fn_blur(item)" @focus="fn_focus(item)" :style="item.cart>0||item._showInput?'display:block':'display:none'" />
                    <div class="goods_num_add" @click="fn_add(item)">+</div>
                  </div>
                </div>
                <div class="goods_count_tag" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                  <!-- <div>折后约  {{item.rrp}}</div> -->
                  <div>毛利率  {{item.grossProfit}}%</div>
                  <div>零售价  {{item.rrp}}</div>
                </div>
              </div>
              <!--一块购，限时抢购-->
              <div v-if="template==202 || template==203">
                <div class="goods_tag_box">
                  <div class="goods_price" v-if="item.purchaseprice != -1 && item.purchaseprice != -2">
                    <div class="actual_price">￥{{item.purchaseprice}}</div>
                    <div class="original_price">￥{{item.mp}}</div>
                  </div>
                  <div class="goods_price" v-else>
                    <div class="actual_price fontsize20" v-if="item.purchaseprice == -1">￥认证后可见</div>
                    <div class="actual_price fontsize20" v-if="item.purchaseprice == -2">￥签约后可见</div>
                  </div>
                  <div class="goods_spe">中包装：{{item.medpacknum}}{{item.unitName}}</div>
                </div>
                <div class="goods_btn" v-if="template==203" @click="toDetail(item)">去参团</div>
                <div class="goods_btn" v-if="template==202" @click="toDetail(item)">立即抢购</div>
              </div>
            </div>
          </div>
        </div>
        
      </van-list>
      <div class="no_goods" v-if="finished && goodsDataList.length == 0"><img src="../../assets/noGoods.png" /></div>
<!--    </van-pull-refresh> -->
  </div>
</template>

<script>
  export default {
    data() {
      return {
        onlyActivity:false,
        hideBrandBox:true,
        codes:null,//活动码
        template:null,//模板类型
        tag:null,//给商品添加的标签
        dtlImg:null,//专区广告图
        brandList:[],//显示的品牌数据
        isLoading:false,//上拉刷新
        loading:true,//下拉加载
        pageIndex:1,//初始页码
        finished:false,//可以下拉加载
        goodsDataList:[],//商品数据列表
        timer:null,//定时器
        fillWidth:0,//团购填充宽度
        countDown:{
          h: 0,
          m: 0,
          s: 0
        },//倒计时
        brandno:'',//品牌号
        brandIndex:0,//品牌下标
        manuListIndex:0,//选中品牌manuList下标
        timelimitTips:[],//限时抢购场次
        teamBuyWidth:[],//团购阶梯
        dtlImgLoading:true,//广告图是否加载中
        canToggleDtlImg:false,//是否能切换广告图
        imgOnerror:'',//图片加载出错时显示的图片
      }
    },
    mounted(){
      this.codes = this.$route.query.codes;
      this.template = this.$route.query.template;
      this.tag = this.$route.query.tag;
      this.dtlImg = this.$route.query.dtlImg;
      this.imgOnerror = this.imgOnerror = "this.src='" + this.$route.query.dtlImg + "'";
      this.loading = true;
      let goodsActivityHomeData = sessionStorage.getItem("goodsActivityHomeData");
      if(goodsActivityHomeData){
        goodsActivityHomeData = JSON.parse(goodsActivityHomeData);
        this.scrollTop = goodsActivityHomeData.scrollTop;
        this.pageIndex = goodsActivityHomeData.pageIndex;
        this.brandno = goodsActivityHomeData.brandno;
        this.hideBrandBox = goodsActivityHomeData.hideBrandBox;
        this.onlyActivity = goodsActivityHomeData.onlyActivity;
        this.canToggleDtlImg = goodsActivityHomeData.canToggleDtlImg;
        //清除缓存
        sessionStorage.removeItem("goodsActivityHomeData");
        if(this.template == 204){
          this.getBrandListData(true);
        }else{
          this.dtlImgLoading = false;
          this.getGoodsListData(true);
        }
      }else{
        this.dtlImgLoading = false;
        if(this.template == 204){
          this.getBrandListData();
        }else{
          this.getGoodsListData();
        }
      }
    },
    methods:{
      toBrandZone(name,manuNo){
        //缓存回到此页面时需要的信息
        let data = {
          scrollTop:document.documentElement.scrollTop || 0,//滚动条位置
          pageIndex:this.pageIndex,//页码
          brandno:this.brandno,//品牌号
          hideBrandBox:this.hideBrandBox,
          onlyActivity:this.onlyActivity,
          canToggleDtlImg:this.canToggleDtlImg,
        }
        data = JSON.stringify(data);
        sessionStorage.setItem(
          "goodsActivityHomeData",
          data
        );
        this.$router.push({
          path: '/brandZone',
          query:{
            name:name,
            manuNo:manuNo,
            codes:this.codes,
            brandno:this.brandno,
            onlyActivity:this.onlyActivity,
            brandName:this.brandList[this.brandIndex].brandname,
            imgUrl:this.$route.query.dtlImg
          }
        })
      },
      showBrandBox(){
        this.hideBrandBox = !this.hideBrandBox;
      },
      goCart(){
        this.$router.push({
          path: '/shoppingCart'
        })
      },
      getBrandListData(hasCache){//获取品牌
        JNB.query({
          serverName: "goodsServer",
          cls: "MainPageModule",
          method: "pageInfo",
          params:{
            identity:this.codes,
            onlyActivity:this.onlyActivity,
            jsonStr:JSON.stringify({keyword:"",brandno:this.brandno})
          },
          page:1,
          count:10,
          callback :(data)=>{
            data = JSON.parse(data);
            if(data.code == 200){
              //根据品牌号，确定品牌下标
              //if(this.brandList.length == 0){
                
                if(data.data.actObj && data.data.actObj.length>0){
                  let bl = false;
                  for(let i= 0;i<data.data.actObj.length;i++){
                    if(data.data.actObj[i].brandno == this.brandno){
                      data.data.actObj[i]._selected = true;
                      this.brandIndex = i;
                      bl = true;
                    }else{
                      data.data.actObj[i]._selected = false;
                    }
                  }
                  if(!bl){
                    this.brandIndex = 0;
                  }
                  if(this.canToggleDtlImg){
                    let urlArr = this.dtlImg.split('/');
                    urlArr.pop();
                    this.dtlImg = urlArr.join('/') + '/' + data.data.actObj[this.brandIndex].brandname + '.png';
                  }
                  this.dtlImgLoading = false;
                  if(this.brandIndex == 0){
                    data.data.actObj[0]._selected = true;
                    this.brandno = data.data.actObj[0].brandno;
                  }
                //}
                
                this.brandList = data.data.actObj;
                this.getGoodsListData();//获取商品
              }
            }
          }
        })
      },
      getGoodsListData(hasCache){
        if(this.template == 204){//品牌
          JNB.query({
            serverName: "goodsServer",
            cls: "MainPageModule",
            method: "pageInfo",
            params:{
              identity:this.codes,
              onlyActivity:this.onlyActivity,
              jsonStr:JSON.stringify({
                manuname:this.brandList[this.brandIndex].manuList[this.manuListIndex].manuName,
                brandno:this.brandno,
                manuno:this.brandList[this.brandIndex].manuList[this.manuListIndex].manuNo,
              })
            },
            page:1,
            count:4,
            callback :(data)=>{
              data = JSON.parse(data);
              this.isLoading = false;//如果是上拉刷新就停止刷新
              this.loading = false;//加载完成
              if(data.code == 200){
                if(this.manuListIndex == 0){
                  this.goodsDataList = [];
                }
                if(data.data && data.data.list && data.data.list.length>0){
                  data.data.list.forEach(item => {
                    item.cart = 0;//设置初始购物车数量
                    //item._rate = (((item.rrp-item.vatp)/item.rrp*100).toFixed(0)) + '%';//计算毛利率
                    item._tag = this.tag;
                  })
                  this.$request_img({//获取商品图片
                    context: this,
                    arr: data.data.list
                  })
                  this.goodsDataList.push({
                    name:this.brandList[this.brandIndex].manuList[this.manuListIndex].manuName,
                    manuNo:this.brandList[this.brandIndex].manuList[this.manuListIndex].manuNo,
                    data:data.data.list,
                  })
                }
                if(this.manuListIndex >= this.brandList[this.brandIndex].manuList.length-1){
                  this.finished = true;//锁定下拉加载
                }else{
                  this.manuListIndex = this.manuListIndex + 1;
                }
              }
            },
          })
        }else{
          JNB.query({
            serverName: "goodsServer",
            cls: "MainPageModule",
            method: "pageInfo",
            params:{
              identity:this.codes,
              jsonStr:JSON.stringify({keyword:"",brandno:this.brandno})
            },
            page:hasCache?1:this.pageIndex,
            count:hasCache?this.pageIndex*10:10,
            callback :(data)=>{
              data = JSON.parse(data);
              this.isLoading = false;//如果是上拉刷新就停止刷新
              this.loading = false;//加载完成
              if(data.code == 200){
                if(data.data && data.data.list && data.data.list.length>0){
                  data.data.list.forEach(item => {
                    item.cart = 0;//设置初始购物车数量
                    //item._rate = (((item.rrp-item.vatp)/item.rrp*100).toFixed(0)) + '%';//计算毛利率
                    item._tag = this.tag;
                  })
                  this.$request_img({//获取商品图片
                    context: this,
                    arr: data.data.list
                  })
                  
                  if(data.data.page.value.pageIndex == 1 || hasCache){
                    this.goodsDataList = data.data.list;
                    if(this.template == 203){//团购
                      //获取最大值
                      let max = 0;
                      for (let i=0;i<data.data.actObj.ladoffArray.length;i++) {
                        if(data.data.actObj.ladoffArray[i].ladnum>max){
                          max = data.data.actObj.ladoffArray[i].ladnum;
                        }
                      }
                      //设置百分比
                      for (let i=0;i<data.data.actObj.ladoffArray.length;i++) {
                        data.data.actObj.ladoffArray[i]._width = (data.data.actObj.ladoffArray[i].ladnum/max*100).toFixed(2)+'%';
                      }
                      this.teamBuyWidth = data.data.actObj.ladoffArray;
                      this.fillWidth = (data.data.actObj.currNums/max*100).toFixed(2)+'%';
                      this.secondKill(
                        this.$stringToDate(data.data.actObj.now),
                        data.data.actObj.edate
                      )
                    }else if(this.template == 202){
                      // let timelimitTips = [];
                      // for (let i = 0; i < data.data.timeArray.length; i++) {
                      //   data.data.timeArray[i]._time = data.data.timeArray[i].sdate.substring(0,5);
                      //   if(data.data.timeArray[i].sdate == data.data.sdate 
                      //     && data.data.timeArray[i].edate == data.data.edate 
                      //   ){
                      //     data.data.timeArray[i]._isActive = true;
                      //     data.data.timeArray[i]._txt = '正在开抢';
                      //   }else{
                      //     data.data.timeArray[i]._isActive = false;
                      //     let contrast = function (date,now) {
                      //       date = new Date((now.substring(0,11) + date));
                      //       now = new Date(now);
                      //       if(now - date >0) return true
                      //       return false
                      //     }
                      //     //结束时间小于当前时间
                      //     if(contrast(data.data.timeArray[i].edate,data.data.now)){
                      //       data.data.timeArray[i]._txt = '已结束';
                      //     }else if(!contrast(data.data.timeArray[i].edate,data.data.now)){
                      //       //开始时间大于当前时间
                      //       if(contrast(data.data.timeArray[i].sdate,data.data.now)){
                      //         data.data.timeArray[i]._txt = '已开抢';
                      //       }else{
                      //         data.data.timeArray[i]._txt = '即将开抢';
                      //       }
                      //     }
                      //   }
                      //   timelimitTips.push(data.data.timeArray[i]);
                      // }
                      // this.timelimitTips = timelimitTips;
                    }else if(this.template == 204){
                      if(this.brandList.length == 0){
                        if(data.data.actObj && data.data.actObj.length>0){
                          for(let i= 0;i<data.data.actObj.length;i++){
                            if(data.data.actObj[i].brandno == this.brandno){
                              data.data.actObj[i]._selected = true;
                            }else{
                              data.data.actObj[i]._selected = false;
                            }
                          }
                        }
                        this.brandList = data.data.actObj;
                      }
                    }
                  }else{
                    this.goodsDataList = this.goodsDataList.concat(data.data.list);
                  }
                  
                  if(data.data.page.value.pageIndex >= data.data.page.value.totalPageCount){
                    this.finished = true;//锁定下拉加载
                  }else{
                    this.pageIndex = this.pageIndex+1;
                  }
                  if(hasCache){
                    setTimeout(() =>{
                      document.documentElement.scrollTop = this.scrollTop;
                    },100)
                  }
                }else{
                  this.finished = true;//锁定下拉加载
                }
              }else{
                this.finished = true;//锁定下拉加载
              } 
            },
          })
        }
      },
      onRefresh(){//上拉刷新
        if(this.timer){//清除原有定时器
          clearInterval(this.timer);
        }
        this.pageIndex = 1;
        this.manuListIndex = 0;
        this.finished = false;//解除下拉加载锁定
        this.getGoodsListData();
      },
      onLoad(){//下拉加载
        this.getGoodsListData();
      },
      //商品详情跳转
      toDetail(obj){
        //缓存回到此页面时需要的信息
        let data = {
          scrollTop:document.documentElement.scrollTop || 0,//滚动条位置
          pageIndex:this.pageIndex,//页码
          brandno:this.brandno,//品牌号
          hideBrandBox:this.hideBrandBox,
          onlyActivity:this.onlyActivity,
          canToggleDtlImg:this.canToggleDtlImg,
        }
        data = JSON.stringify(data);
        sessionStorage.setItem(
          "goodsActivityHomeData",
          data
        );
        this.$router.push({
          path: '/goodsDetail',
          query:{
            sku:obj.sku
          }
        })
      },
      //商品减少
      fn_minus(obj){
        this.$manageGoodNum(this,obj,-1);
      },
      //商品添加
      fn_add(obj){
        this.$manageGoodNum(this,obj,-2);
      },
      //输入商品数量
      fn_blur(obj){
        obj._showInput = false;
        if(obj.cart == ''){
          obj.cart = 0;
        }
        this.$manageGoodNum(this,obj,obj.cart);
      },
      //获取焦点时一直显示input框
      fn_focus(obj){
        obj._showInput = true;
      },
      //点击品牌
      fn_set_brand(idx){
        if(this.loading){
          return
        }
        for(let i= 0;i<this.brandList.length;i++){
          this.brandList[i]._selected = false;
        }
        this.brandIndex = idx;
        this.brandno = this.brandList[idx].brandno;
        this.brandList[idx]._selected = true;
        this.pageIndex = 1;
        this.manuListIndex = 0;
        this.finished = false;//解除下拉加载锁定
        this.loading = true;
        this.goodsDataList = [];//清空原有商品数据
        if(this.canToggleDtlImg){
          let urlArr = this.dtlImg.split('/');
          urlArr.pop();
          this.dtlImg = urlArr.join('/') + '/' + this.brandList[this.brandIndex].brandname + '.png';
          this.dtlImgLoading = false;
        }
        this.getGoodsListData();
      },
      //切换是否只显示活动
      toggleOnlyActivity(){
        if(this.loading){
          return
        }
        this.onlyActivity = !this.onlyActivity;
        this.pageIndex = 1;
        this.manuListIndex = 0;
        this.finished = false;//解除下拉加载锁定
        this.loading = true;
        this.goodsDataList = [];//清空原有商品数据
        this.getBrandListData();
      },
      // 设置倒计时
      secondKill(date,eDate){
        let endDate = this.$stringToDate(
          date.getFullYear() +
            "-" +
            (Number(date.getMonth()) + 1) +
            "-" +
            date.getDate() +
            " " +
            eDate
        );
        let times = Math.floor((endDate - date) / 1000);
        let _this = this;
        if (times >= 0) {
          _this.timer = setInterval(function() {
            times--;
            _this.countDown.h = Math.floor(times / 60 / 60);
            _this.countDown.m = Math.floor(times / 60) % 60;
            _this.countDown.s = times % 60;
            if (times <= 0) {
              clearInterval(_this.timer);
            }
          }, 1000);
        }
      },
    }
    
  }
</script>

